/* import swiper styles */
@import "../node_modules/swiper/swiper-bundle.min.css";
.swiper-button-next, .swiper-button-prev {
  top: calc(55% + 60px) !important;
}
.swiper-button-prev, .swiper-rtl .swiper-button-next {
  left: var(--swiper-navigation-sides-offset, 10%);
  right: auto;
}
.swiper-button-next, .swiper-rtl .swiper-button-prev {
  right: var(--swiper-navigation-sides-offset, 10%);
  left: auto;
}
.swiper-arr {
  position: relative;
}

.swiper {
  width: 1180px;
  min-height: 100%;
}

@media (max-width:1439px) {
  .swiper-button-next, .swiper-button-prev {
    top: calc(55% + 40px) !important;
  }
  .swiper {
    width: 800px;
    /* height: 326px; */
  }
}
@media (max-width:1023px) {
  .swiper-button-next, .swiper-button-prev {
    top: calc(41% + 60px) !important;
  }
  .swiper {
    width: 655px;
  }
}
@media (max-width:767px) {
  .swiper {
    width: 280px;
  }
}

/* import swiper styles */
@font-face {
  font-family: Arial;
  src: local("Arial Regular");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Arial;
  src: local("Arial Bold");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Arial;
  src: local("Arial Black");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

html {
  scroll-behavior: smooth;
  position: relative;
  box-sizing: border-box;
}


*,
*::before,
*::after {
  box-sizing: inherit;
  margin-block-end: 0;
  margin-block-start: 0;
  margin-bottom: 0;
  margin-inline-end: 0;
  margin-inline-start: 0;
}


body {
  min-width: 320px;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: var(--c-white-txt);
  background-color: var(--с-brown-dark-bg);
}

img {
  max-width: 100%;
  height: auto;
}


button {
  padding: 0;
  cursor: pointer;
  outline: none;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
p,
figure,
fieldset {
  margin: 0;
}

fieldset {
  padding-block-start: 0;
  padding-inline-start: 0;
  padding-inline-end: 0;
  padding-block-end: 0;
  min-inline-size: 0;
  border-width: 0;
  border-style: none;
}

iframe {
  border: none;
}

/*
Доступно скрываю элементы
*/

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

/*
У секции добавляю вертикальные паддинги,
это половина от расстояния между блоками
*/

section {
padding: 50px 0;
}

@media (max-width:1439px) {
  section {
    padding: 35px 0;
  }
}

@media (max-width:1023px) {
  section {
    padding: 30px 0;
  }
}

@media (max-width:767px) {
  section {
    padding: 15px 0;
  }
}



